:root {
  --color-background: #031a50;
  --color-red: #812d00;
  --color-mono: #2a2c2d;
  --color-text: var(--color-white);
  --color-white: #fff;
  --color-button-background: var(--color-button-background-default);
  --color-button-border: #003b8f;
  --color-button-background-default: #003b8f;
