  --color-button-background-stop: #8f3700;
  --color-button-background-stop-hover: #812d00;
  --color-button-text-default: #b7d5ff;
  --color-icon: #fff;
}

:root, [data-selected-theme="blue"] {
  --color-background: #031a50;
