header {
  z-index: 400;
  justify-content: center;
  width: 100%;
  height: 60px;
  display: flex;
  position: absolute;
}

.header-container {
  opacity: 0;
  background: #fff;
  border-radius: 0 0 20px 20px;
  justify-content: center;
  align-items: center;
  width: 150px;
  height: auto;
  margin-top: 0;
  padding: .75rem .5rem;
  transition: all .35s ease-in-out;
  display: flex;
  position: relative;
  top: -70px;
  box-shadow: 0 0 4px #00000040;
