button {
  cursor: pointer;
  background: var(--color-button-background);
  color: var(--color-button-text-default);
  border: 1px solid;
  border-color: var(--color-button-border);
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 40px;
  margin: 0;
  padding: .5rem;
  font-family: inherit;
  font-size: 1rem;
  line-height: 1;
  transition: background .1s ease-in-out;
  display: flex;
}

@media (width >= 615px) {
  button {
    min-height: 60px;
  }
}

button svg {
  width: 32px;
  height: 32px;
}

@media (width >= 1024px) {
  button svg {
    width: 64px;
    height: 64px;
  }
}

button svg path {
  fill: var(--color-icon);
}

button:hover {
