}

.buttons-container-breaks button {
  font-size: 1.25rem;
}

.popup {
  z-index: 1000;
  background-color: #fff;
  width: 80%;
  max-width: 800px;
  padding: 20px;
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px #0000004d;
}

.popup iframe, iframe {
  width: 100%;
  height: 100%;
}

.popup #closePopup {
  cursor: pointer;
  background: none;
  border: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

@keyframes slide-in-right {
  0% {
    opacity: 0;
    transform: translateX(1000px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.slide-in-right {
  animation: .5s cubic-bezier(.25, .46, .45, .94) both slide-in-right;
}
